import { useState, useCallback, useEffect } from 'react';
import Header from 'components/header/header';
import SectionCrosses from 'components/SectionCrosses/SectionCrosses';
import SectionNeedHelp from 'components/sectionNeedHelp/sectionNeedHelp';
import Footer from 'components/footer/footer';
import translations from 'components/LanguageSelect/translations';
import imgBaner from 'images/sectionCrosses/sectionCrosses.jpg';

const CrossesPage = () => {
  useEffect(() => {
      window.scrollTo(0, 0);
  }, []);
  
  const [language, setLanguage] = useState('ua');

  const t = translations.navlink[language];

  const navItems = [
    {
      type: 'link',
      to: '/',
      label: t.main,
      delay: 0.05,
    }
  ];

  const handleLanguageChange = useCallback(
    langCode => {
      if (langCode !== language) {
        setLanguage(langCode);
      }
    },
    [language]
  );

  const title = '–†–∏—Ç—É–∞–ª—å–Ω—ñ —Ö—Ä–µ—Å—Ç–∏';
  const imgBannerDescription = '–î–æ –≤—Å—Ç–∞–Ω–æ–≤–ª–µ–Ω–Ω—è –ø–æ—Å—Ç—ñ–π–Ω–æ–≥–æ –ø–∞–º‚Äô—è—Ç–Ω–∏–∫–∞ –æ–¥—Ä–∞–∑—É –ø—ñ—Å–ª—è –ø–æ—Ö–æ—Ä–æ–Ω—É –Ω–∞ –º–æ–≥–∏–ª—ñ —Ö—Ä–µ—â–µ–Ω–æ–≥–æ —Ö—Ä–∏—Å—Ç–∏—è–Ω–∏–Ω–∞ —Å—Ç–∞–≤–ª—è—Ç—å —Ä–∏—Ç—É–∞–ª—å–Ω–∏–π —Ö—Ä–µ—Å—Ç, –Ω–∞ —è–∫–æ–º—É –∑–∞–∫—Ä—ñ–ø–ª–µ–Ω–æ —Ç–∞–±–ª–∏—á–∫—É –∑ –ø—Ä—ñ–∑–≤–∏—â–µ–º, —ñ–º‚Äô—è–º —Ç–∞ –¥–∞—Ç–∞–º–∏ –Ω–∞—Ä–æ–¥–∂–µ–Ω–Ω—è —Ç–∞ —Å–º–µ—Ä—Ç—ñ –ø–æ–º–µ—Ä–ª–æ–≥–æ. –¶–µ –¥–∞–≤–Ω—è —Ö—Ä–∏—Å—Ç–∏—è–Ω—Å—å–∫–∞ —Ç—Ä–∞–¥–∏—Ü—ñ—è, –æ—Å–æ–±–ª–∏–≤–æ —à–∞–Ω–æ–≤–∞–Ω–∞ —É –ø—Ä–∞–≤–æ—Å–ª–∞–≤–Ω–æ–º—É —Å–≤—ñ—Ç—ñ. –ü–æ—Ö–æ—Ä–æ–Ω–Ω–∏–π –¥—ñ–º ¬´–û–º–µ–≥–∞¬ª –±–µ—Ä–µ –Ω–∞ —Å–µ–±–µ –∫–ª–æ–ø—ñ—Ç —ñ–∑ –≤–∏–≥–æ—Ç–æ–≤–ª–µ–Ω–Ω—è —Ç–∞ –≤—Å—Ç–∞–Ω–æ–≤–ª–µ–Ω–Ω—è —Ä–∏—Ç—É–∞–ª—å–Ω–æ–≥–æ –¥–µ—Ä–µ–≤‚Äô—è–Ω–æ–≥–æ —á–∏ –º–µ—Ç–∞–ª–µ–≤–æ–≥–æ —Ö—Ä–µ—Å—Ç–∞, —è–∫–∏–π –ø–æ–≤–Ω—ñ—Å—Ç—é –≤—ñ–¥–ø–æ–≤—ñ–¥–∞—î —Ü–µ—Ä–∫–æ–≤–Ω–∏–º –∫–∞–Ω–æ–Ω–∞–º —Ç–∞ –∫—É–ª—å—Ç—É—Ä–Ω–∏–º —Ç—Ä–∞–¥–∏—Ü—ñ—è–º –Ω–∞—à–æ–≥–æ –Ω–∞—Ä–æ–¥—É.';

  return (
    <>
      <Header navItems={navItems} handleLanguageChange={handleLanguageChange} language={language} />
      <main>
        <SectionCrosses title={title} imgBaner={imgBaner} imgBannerDescription={imgBannerDescription} />
        <SectionNeedHelp />
      </main>
      <Footer />
    </>
  );
};

export default CrossesPage;